import { useParams } from 'react-router'
import { projects } from '../../portfolio'
import './ProjectPage.css'
import images from '../../assets/images'

const ProjectPage = () => {
  const { id } = useParams()

  const project = projects.find((obj) => obj.id.toString() === id)

  const mainImageUrl = `../public/assets/images/project_${id}/main_img.png`
  const secondaryImageUrl = `/assets/images/project_${id}/secondary_img.png`
  const tertiaryImageUrl = `/assets/images/project_${id}/tertiary_img.png`

  return (
    <section id='projectPage' className='section projects'>
      <h2 className='section__title'> {project.name} </h2>
      <img src={images.main[id]} alt='' />
      <img src={images.secondary[id]} alt='' />
      <img src={images.tertiary[id]} alt='' />
    </section>
  )
}

export default ProjectPage
